import { Grid, Typography, useMediaQuery, useTheme } from '@mui/material'

interface ClubInfoGridProps {
  label: string
  value: string
}

const ClubInfoGrid: React.FC<ClubInfoGridProps> = ({ label, value }) => {
  const theme = useTheme()
  const isDesktopOrTablet = useMediaQuery(theme.breakpoints.up('sm'))

  return (
    <Grid item xs={6}>
      <Typography variant={isDesktopOrTablet ? 'h4' : 'h5'} display="inline">
        {label}&nbsp;
      </Typography>
      <Typography display="inline">{value}</Typography>
    </Grid>
  )
}

export default ClubInfoGrid
