import { useAuthUser } from 'react-auth-kit'

import { Box, Button, Chip, Modal, TextareaAutosize, Typography } from '@mui/material'
import { useTheme } from '@mui/material/styles'
import useMediaQuery from '@mui/material/useMediaQuery'

import { chipState } from 'src/data/applyResultChip'

import type { ApplyView } from 'src/models/apply'

interface AnswerModalProps {
  open: boolean
  onClose: React.MouseEventHandler<HTMLButtonElement>
  application: ApplyView
}

const AnswerModal: React.FC<AnswerModalProps> = ({ open, onClose, application }) => {
  const theme = useTheme()
  const isNotMobile = useMediaQuery(theme.breakpoints.up('sm'))
  const authInfo = useAuthUser()()

  const { questionAndAnswer, result } = application
  const [chipLabel, chipColor] = chipState[result]

  return (
    <Modal open={open} onClose={onClose}>
      <Box sx={style}>
        <Box justifyContent="center">
          <Typography variant="h3" mb={1} textAlign={'center'}>
            {authInfo?.name} 님 의 지원서 <Chip size="small" color={chipColor} label={chipLabel} />
          </Typography>
          <Typography variant="subtitle2" textAlign="center">
            {authInfo?.studentId} / {authInfo?.department}
          </Typography>
          <Typography variant="subtitle2" mb={2} textAlign={'center'}>
            {authInfo?.email} / {authInfo?.phoneNumber}
          </Typography>
        </Box>
        <Box display="flex" alignItems="center" justifyContent="space-around">
          <Box width={isNotMobile ? '60%' : '90%'}>
            {questionAndAnswer !== null ? (
              questionAndAnswer.map((qa, index) => {
                return (
                  <Box key={index}>
                    <Typography variant="body2">{qa.question.contents}</Typography>
                    {qa.question.isCheckBox ? (
                      <Box style={AnswerBoxStyle}>
                        {qa.question.options?.map((option, index) => (
                          <Typography
                            key={index}
                            variant="body2"
                            display={'block'}
                            color={qa.answer?.optionIndices?.includes(index) ? 'primary' : theme.palette.grey[500]}
                          >
                            {option}
                          </Typography>
                        ))}
                      </Box>
                    ) : (
                      <TextareaAutosize
                        style={AnswerBoxStyle}
                        maxRows={4}
                        minRows={3}
                        value={qa.answer?.text ?? ''}
                        disabled
                      />
                    )}
                  </Box>
                )
              })
            ) : (
              <Typography textAlign={'center'}>지원서 문항이 존재하지 않습니다.</Typography>
            )}
          </Box>
        </Box>
        <Box marginTop={2} display="flex" justifyContent="center">
          <Button variant="outlined" onClick={onClose}>
            닫기
          </Button>
        </Box>
      </Box>
    </Modal>
  )
}

export default AnswerModal

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: '80%',
  maxWidth: '600px',
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4
}

export const AnswerBoxStyle = {
  border: '1px solid lightgrey',
  borderRadius: '5px',
  padding: '8px',
  margin: '10px 0px',
  width: '100%',
  fontFamily: 'Pretendard'
}
